@import url(https://fonts.googleapis.com/css?family=Lora:400,400i,700,700i);
html, body {
  height: 100%;
  font-family: 'Lora', serif;
  background: #fff;
  color: #666;
}

.btn-primary {
  background: #666 !important;
  border: none !important;
  outline: none !important;
  width: 100%;
}

.form-control:focus {
  border-color: #666;
}

.btn-primary:hover, .btn-primary:active, .btn-primary:focus {
  background: #666 !important;
  outline: none !important;
  border: none !important;
}

.navbar-brand {
  display: block;
  font:url(/meme-factory/static/media/avengero.6cf5fc94.ttf);
  text-align: center;
  color: #000000;
  margin: 0px 0px 20px 0px;
  padding-bottom: 15px;
  border-bottom: 1px solid #0000;
}

.navbar-brand:focus, .navbar-brand:hover, .navbar-brand:active {
  color: #FFF;
}

.modal-content {
  
  width: 800px !important;
  height: 800px !important;
}


.meme-gen-modal {
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
}

.modal-body {
  display: flex;
  border-radius: 0px;
  border: 1px none #000000;
  background-color: rgba( 246, 245, 243, 1.00 );
}

.modal-header {
  background-color: rgba( 246, 245, 243, 1.00 );
}

.meme-form {
  margin-left: 20px;
  padding-left: 20px;
  width: 300px;
  border-left: 1px solid #e9ecef;
}

.form-control:focus {
  box-shadow: none;
}

.image-holder {
  width: 168px;
  height: 168px;
  margin: 2.5px 2px;
  position: relative;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.1);
}

.image-holder:hover {
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.3);
}

.row {
  margin: 0 auto;
  justify-content: center;
}

.main-content {
  display: flex;
}

.sidebar {
  min-width: 300px;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(255,255,255,.5)), to(rgba(255,255,255,.5))), url(/meme-factory/static/media/meme_factory.a2b8c7f0.jpg)   right repeat-y;
  background: -webkit-linear-gradient(rgba(255,255,255,.5), rgba(255,255,255,.5)), url(/meme-factory/static/media/meme_factory.a2b8c7f0.jpg)   right repeat-y;
  background: linear-gradient(rgba(255,255,255,.5), rgba(255,255,255,.5)), url(/meme-factory/static/media/meme_factory.a2b8c7f0.jpg)   right repeat-y;  min-height: 100vh;

  margin-right: 7.5px;
  padding: 20px;
  max-width: 300px;
}

.sidebar p {
  margin-bottom: 30px;
  font-size: 15.12px;
  display: inline-block;
  color: #9999;
  letter-spacing: 1px;
  font-weight: 300;
  text-align: center;
}

.content {
  display: flex;
  flex-wrap: wrap;
  margin-top: 10px;
}

.meme-bottom-caption {
  position: absolute;
  font-size: 20px;
  display: block;
  width: 100%;
  font-family: Impact;
  text-align: center;
  text-transform: uppercase;
  bottom: 0;
  padding: 5px;
  color: #fff;
  letter-spacing: 1px;
  background: -webkit-gradient(linear, right top, left top, from(rgb(51, 51, 51)), to(rgba(51, 51, 51, 0.81)));
  background: -webkit-linear-gradient(right, rgb(51, 51, 51), rgba(51, 51, 51, 0.81));
  background: linear-gradient(to left, rgb(51, 51, 51), rgba(51, 51, 51, 0.81));
}

.meme-top-caption {
  position: absolute;
  font-size: 20px;
  display: block;
  width: 100%;
  font-family: Impact;
  text-align: center;
  text-transform: uppercase;
  top: 0;
  padding: 5px;
  color: #fff;
  letter-spacing: 1px;
  background: -webkit-gradient(linear, right top, left top, from(rgb(51, 51, 51)), to(rgba(51, 51, 51, 0.81)));
  background: -webkit-linear-gradient(right, rgb(51, 51, 51), rgba(51, 51, 51, 0.81));
  background: linear-gradient(to left, rgb(51, 51, 51), rgba(51, 51, 51, 0.81));
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.ownUploadButton{
  background-color: #008CBA; /* Green */
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  -webkit-transition-duration: 0.4s; /* Safari */
  transition-duration: 0.4s;  
  border-radius: 8px;
}
.ownUploadButton:hover {
  box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24),0 17px 50px 0 rgba(0,0,0,0.19);
}
.uploadImage{
  background-color: #008CBA; /* Green */
  border: none;
  color: white;
  padding: 7px 22px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  -webkit-transition-duration: 0.4s; /* Safari */
  transition-duration: 0.4s;  
  border-radius: 8px;
}
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
 
